import { useState } from 'react';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from '../../../components/ui/dialog';
import { Button } from '../../../components/ui/button';
import { Input } from '../../../components/ui/input';
import { Label } from '../../../components/ui/label';
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from '../../../components/ui/tabs';

// =======================================
// Props
// =======================================
interface AuthDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onLogin: (email: string, password: string) => Promise<void>;
  onSignup: (email: string, password: string, name: string) => Promise<void>;
  isLoading?: boolean;
}

// =======================================
// Component
// =======================================
// /
export function AuthDialog({
  isOpen,
  onClose,
  onLogin,
  onSignup,
  isLoading,
}: AuthDialogProps) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [error, setError] = useState('');

  // 
  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!email || !password) {
      setError('');
      return;
    }

    try {
      await onLogin(email, password);
      // Reset form
      setEmail('');
      setPassword('');
      setName('');
    } catch (error) {
      console.error('Login failed:', error);
      // 
      if (error instanceof Error) {
        setError(error.message);
      } else {
        setError('');
      }
    }
  };

  // 
  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!email || !password || !name) {
      setError('');
      return;
    }

    try {
      await onSignup(email, password, name);
      // 
      setError('');
      alert(
        '',
      );
      // Reset form
      setEmail('');
      setPassword('');
      setName('');
      onClose();
    } catch (error) {
      console.error('Signup failed:', error);
      // 
      if (error instanceof Error) {
        setError(error.message);
      } else {
        setError('');
      }
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent
        placement="raised"
        size="auth"
        className="items-stretch gap-5 sm:gap-6 rounded-2xl sm:rounded-2xl"
      >
        <div className="w-full flex flex-col gap-4 sm:gap-6">
          <DialogHeader className="space-y-2 sm:space-y-3 text-center sm:text-center">
            <div className="flex items-center justify-center mb-1.5">
              <div className="w-12 h-12 sm:w-16 sm:h-16 rounded-2xl sm:rounded-2xl bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center text-2xl sm:text-3xl shadow-md text-white">
                
              </div>
            </div>
            <DialogTitle className="text-xl sm:text-2xl font-semibold text-gray-900">
              Today's Cocktail
            </DialogTitle>
            <DialogDescription className="text-sm sm:text-base text-gray-500">
              
            </DialogDescription>
          </DialogHeader>

          <Tabs defaultValue="login" className="mt-2 sm:mt-3 w-full">
            <TabsList className="grid w-full grid-cols-2 bg-gray-100 h-11 sm:h-12 rounded-2xl p-1">
              <TabsTrigger
                value="login"
                className="data-[state=active]:bg-white data-[state=active]:shadow-lg text-sm sm:text-base py-2 rounded-2xl"
              >
                
              </TabsTrigger>
              <TabsTrigger
                value="signup"
                className="data-[state=active]:bg-white data-[state=active]:shadow-lg text-sm sm:text-base py-2 rounded-2xl"
              >
                
              </TabsTrigger>
            </TabsList>

            <TabsContent
              value="login"
              className="space-y-4 sm:space-y-5 mt-4 sm:mt-5"
            >
              <form onSubmit={handleLogin} className="space-y-3 sm:space-y-4">
                {error && (
                  <div className="p-2.5 sm:p-3 bg-red-50 border border-red-200 rounded-2xl text-red-800 text-sm sm:text-base">
                    {error}
                  </div>
                )}
                <div className="space-y-1.5">
                  <Label
                    htmlFor="login-email"
                    className="text-sm sm:text-base font-medium text-gray-700"
                  >
                    
                  </Label>
                  <Input
                    id="login-email"
                    type="email"
                    placeholder="your@email.com"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    disabled={isLoading}
                    className="h-11 sm:h-12 text-base"
                  />
                </div>
                <div className="space-y-1.5">
                  <Label
                    htmlFor="login-password"
                    className="text-sm sm:text-base font-medium text-gray-700"
                  >
                    
                  </Label>
                  <Input
                    id="login-password"
                    type="password"
                    placeholder=""
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                    disabled={isLoading}
                    className="h-11 sm:h-12 text-base"
                  />
                </div>
                <Button
                  type="submit"
                  className="w-full h-11 sm:h-12 bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white shadow-md mt-2 sm:mt-3 text-base"
                  disabled={isLoading}
                >
                  {isLoading ? '...' : ''}
                </Button>
              </form>
            </TabsContent>

            <TabsContent
              value="signup"
              className="space-y-4 sm:space-y-5 mt-4 sm:mt-5"
            >
              <form onSubmit={handleSignup} className="space-y-3 sm:space-y-4">
                {error && (
                  <div className="p-2.5 sm:p-3 bg-red-50 border border-red-200 rounded-2xl text-red-800 text-sm sm:text-base">
                    {error}
                  </div>
                )}
                <div className="space-y-1.5">
                  <Label
                    htmlFor="signup-name"
                    className="text-sm sm:text-base font-medium text-gray-700"
                  >
                    
                  </Label>
                  <Input
                    id="signup-name"
                    type="text"
                    placeholder=""
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                    disabled={isLoading}
                    className="h-11 sm:h-12 text-base"
                  />
                </div>
                <div className="space-y-1.5">
                  <Label
                    htmlFor="signup-email"
                    className="text-sm sm:text-base font-medium text-gray-700"
                  >
                    
                  </Label>
                  <Input
                    id="signup-email"
                    type="email"
                    placeholder="your@email.com"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    disabled={isLoading}
                    className="h-11 sm:h-12 text-base"
                  />
                </div>
                <div className="space-y-1.5">
                  <Label
                    htmlFor="signup-password"
                    className="text-sm sm:text-base font-medium text-gray-700"
                  >
                    
                  </Label>
                  <Input
                    id="signup-password"
                    type="password"
                    placeholder=""
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                    disabled={isLoading}
                    minLength={6}
                    className="h-11 sm:h-12 text-base"
                  />
                </div>
                <Button
                  type="submit"
                  className="w-full h-11 sm:h-12 bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white shadow-md mt-2 sm:mt-3 text-base"
                  disabled={isLoading}
                >
                  {isLoading ? '...' : ''}
                </Button>
              </form>
            </TabsContent>
          </Tabs>
        </div>
      </DialogContent>
    </Dialog>
  );
}
