/**
 * 
 * @param id - ID
 * @param data - 
 * @returns Promise<Cocktail> 
 */
export interface UpdateCocktailRequest {
  cocktail: {
    name?: string;
    name_ja?: string;
    glass?: string;
    glass_ja?: string;
    description?: string;
    instructions?: string;
    instructions_ja?: string;
    base?: string;
    strength?: string;
    technique?: string;
    image_url_override?: string;
  };
}

export async function updateCocktail(id: number, data: UpdateCocktailRequest): Promise<Cocktail> {
  const updated = await apiPut(`/api/v1/admin/cocktails/${id}`, data);
  // 
  try {
    sessionStorage.removeItem(`cocktail_detail_${id}`);
    // 
    const keysToRemove: string[] = [];
    for (let i = 0; i < sessionStorage.length; i++) {
      const key = sessionStorage.key(i);
      if (key && key.startsWith('cocktails_')) {
        keysToRemove.push(key);
      }
    }
    keysToRemove.forEach((k) => sessionStorage.removeItem(k));
  } catch {
    // 
  }
  return updated;
}
